/* 두 번째 섹션 EarthAnimation을 위한 수정된 CSS */
.earthSection {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black; /* 배경을 우주 테마로 */
}

canvas {
    display: block;
    width: 100%;
    height: 100%;
    position: relative; /* 절대 위치 대신 상대 위치 사용 */
}