$planetCount: 9;
$planetSize: 1200px; // Size of each planet
$planetSpacing: 2300px; // Space between each planet
$planetTransitionSpeed: 2s;
$mainPlanetViewAmount: 280; // Less means we see more of the planet at the bottom
$globalPerspective: 800px;
$sceneAxisTilt: -20deg;
$menuSpacing: 14px;

// Colors
$mercury:#E8927C;
$venus: #b45d15;
$earth: #26daaa;
$mars: #e55f45;
$jupiter: orange;
$saturn: #b29d81;
$uranus: #8dcdd8;
$neptune: #4f83e2;
$pluto: #FF8732;

@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,700');

.solar_systm {
  transform-style: preserve-3d;
  pointer-events: none;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
}

* {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

%absPosition {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  margin: auto;
}

@mixin makePlanet($outer, $inner, $texture) {
  background: url($texture);
  box-shadow: 0 (-590px) 150px black inset, 0 0px 130px 40px $inner inset, 0 0px 23px 4px $inner inset, 0 -10px 130px $outer;
}

%trajectory {
  border: 2px dashed white;
  position: absolute;
  border-radius: 3400px;
  background: none !important;
  z-index: -2;
}

@mixin makeTrajectory($width, $left, $top) {
  @extend %trajectory;
  width: $width;
  height: $width;
  left: $left;
  top: $top;
}

@mixin makeMoon($left, $top, $scale, $texture) {
  left: $left;
  top: $top;
  transform: scale($scale);
  background: url($texture);
  z-index: -1;
  box-shadow: 0px -30px 30px 10px black inset;
}

%planet {
  height: $planetSize;
  width: $planetSize;
  border-radius: $planetSize / 2;
  background: red;
  transition: transform 2.8s .23s cubic-bezier(0.33, 0, 0, 1), opacity 2s .8s, box-shadow 0s 0s;
  background-size: 1140px 910px !important;
  top: auto;
  bottom: - $planetSize + $mainPlanetViewAmount;

  & .moon {
    height: 200px;
    width: 200px;
    border-radius: $planetSize / 2;
    background: red;
    position: absolute;
    text-align: center;
    color: white;
    text-transform: uppercase;
    opacity: 0;
    transition: all .6s .2s;

    h2 {
      font-weight: 100;
      font-size: 40px;
      letter-spacing: 5px;
      margin: 0;
      position: relative;
      top: -120px;
    }

    h3 {
      font-weight: 100;
      font-size: 20px;
      letter-spacing: 5px;
      color: #fea082;
      margin: 0;
      position: relative;
      top: -120px;
    }
  }

  @keyframes planet {
    from {background-position-y: 0px;}
    to {background-position-y: -1000px;}
  }
}

%viewPort {
  position: absolute;
  transform: rotatex($sceneAxisTilt);
  perspective: $globalPerspective;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
}

%trigger {
  @extend %absPosition;

  width: 220px;
  height: 220px;
  border-radius: 550px;
  appearance: none;
  outline: none;
  cursor: pointer;
  z-index: 12;
  left: 18px;
  bottom: 282px;
}

%checkbox {
  appearance: none;
  width: 16px;
  height: 16px;
  border: 2px solid white;
  outline: none;
  border-radius: 20px;
  left: 43px;
  z-index: 2;

  &:checked {
    &::before {
      display:block;
      width: 6px;
      height: 6px;
      background: white;
      position: relative;
      top: 3px;
      left: 3px;
      content: '';
      border-radius: 10px;
    }
  }
}

body {
  overflow: hidden;
  background: black;
  height: 100vh;
  font-family: 'Montserrat', sans-serif;

  .logo {
    color: white;
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 100;
    letter-spacing: 4px;

    span  {
      font-size: 12px;
      color: #f39041;
      display: block;
    }
  }

  label {
    cursor: pointer;

    &.mercury {
      & > .preview {
        background: url('https://www.solarsystemscope.com/images/textures/full/2k_makemake_fictional.jpg') !important;
      }
    }
    &.venus {
      & > .preview {
        background: url('https://nasa3d.arc.nasa.gov/shared_assets/images/ven0aaa2/ven0aaa2-copy-428-321.jpg') !important;
      }
    }
    &.earth {
      & > .preview {
        background: url('https://img00.deviantart.net/04ef/i/2009/114/3/e/new_earth_texture_map_by_lightondesigns.jpg') !important;
      }
    }
    &.mars {
      & > .preview {
        background: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/217233/mars_texture.jpg') !important;
      }
    }
    &.jupiter {
      & > .preview {
        background: url('https://www.jpl.nasa.gov/spaceimages/images/largesize/PIA07782_hires.jpg') !important;
      }
    }
    &.saturn {
      & > .preview {
        background: url('https://www.solarsystemscope.com/images/textures/full/2k_saturn.jpg') !important;
      }
    }
    &.uranus {
      & > .preview {
        background: url('https://img00.deviantart.net/957c/i/2017/165/4/9/uranus_texture_map_by_jcpag2010-db7yjwb.png') !important;
      }
    }
    &.neptune {
      & > .preview {
        background: url('https://img00.deviantart.net/f068/i/2017/165/b/c/neptune_texture_map_by_jcpag2010-dbcjcv5.png') !important;
      }
    }
    &.pluto {
      & > .preview {
        background: url('https://pre00.deviantart.net/4677/th/pre/f/2015/314/4/e/pluto_map__2015_nov_10__by_snowfall_the_cat-d918tlb.png') !important;
      }
    }
  }

  label.menu {
    color: white;
    display: block;
    position: absolute;
    cursor: pointer;
    left: 90px;
    z-index: 2;

    &:after {
      display: block;
      width: 12px;
      height: 12px;
      border: 2px solid white;
      border-radius: 60px;
      content: '';
      z-index: 2;
      position: absolute;
      top: -4px;
      left:-4px;
    }

    &:hover {
      h2,h3 {
        opacity: 1;
      }
    }

    &.mercury h2 .pip {
      background: $mercury;
    }
    &.venus h2 .pip {
      background: $venus;
    }
    &.earth h2 .pip {
      background: $earth;
    }
    &.mars h2 .pip {
      background: $mars;
    }
    &.jupiter h2 .pip {
      background: $jupiter;
    }
    &.saturn h2 .pip {
      background: $saturn;
    }
    &.uranus h2 .pip {
      background: $uranus;
    }
    &.neptune h2 .pip {
      background: $neptune;
    }
    &.pluto h2 .pip {
      background: $pluto;
    }

    & .preview {
      width: 30px;
      height: 30px;
      background: yellow;
      float: left;
      background-size: auto 100% !important;
      position: absolute;
      border-radius: 100px;
      box-shadow: 0 -13px 10px 2px black inset;
    }

    & .info {
      position: relative;
      left: 50px;
      top:1px;
    }

    h2,
    h3 {
      text-transform: uppercase;
      margin: 0;
      font-weight: 100;
      letter-spacing: 2px;
    }

    h2 {
      font-size: 11px;
      opacity: 0.4;
      margin-bottom: 4px;

      & .pip {
        width: 0;
        height: 9px;
        background: #fea082;
        float: left;
        position: relative;
        top: 3px;
        transition: all 0.3s;
        margin-right: 0px;
      }
    }

    h3 {
      font-size: 8px;
      letter-spacing: 1px;
      transition: all 0.3s;
      opacity: 0.3;
    }

    @for $l from 1 through $planetCount {
      &:nth-of-type(#{$l}) {
        $labelSize: 34px;
        $labelSpacing: $menuSpacing;
        $totalHeight: ($labelSize * $planetCount) + ($labelSpacing * ($planetCount)) / 2;

        top: calc(50vh + 100px + ((#{$labelSpacing} + #{$labelSize}) * #{$l}) + 0px - #{$totalHeight});
      }
    }
  }
}

.solar {
  @extend %viewPort;

  &_systm {
    @for $i from 1 through 10 {
      &:nth-of-type(#{$i}) .planet {
        $translateY: 0;
        $translateZ: -$planetSpacing * ($i - 1);
        $opacity: 2 - ($i - 1);
        $rotateX: 4deg;
        $scaleOffset: 0.89;
        transform: translateZ($translateZ) translateY($translateY) rotatex($rotateX) scalex($scaleOffset);
        opacity: $opacity;

        &_description {
          opacity: 1;
          p, a {
            opacity: 0;
          }

          h1, h2 {
            position: relative;
            top: -330px;
            transition: all .5s 0s;
          }
        }
      }
    }

    & .planet {
      @extend %absPosition;
      @extend %planet;

      &.mercury {
        @include makePlanet(rgba(188, 143, 127, 0.6), rgba(234, 205, 199, 0.6), 'https://www.solarsystemscope.com/images/textures/full/2k_makemake_fictional.jpg');
      }

      &.venus {
        @include makePlanet(#b85a07, #ffcb9c, 'https://nasa3d.arc.nasa.gov/shared_assets/images/ven0aaa2/ven0aaa2-copy-428-321.jpg');
      }

      &.earth {
        @include makePlanet(#7894a9, #8cbaff, 'https://img00.deviantart.net/04ef/i/2009/114/3/e/new_earth_texture_map_by_lightondesigns.jpg');
      }

      // 각 행성별 추가 설정은 생략됨
    }
  }
}
